#vstr_settings_page {
  max-width: 100%;
  box-sizing: border-box;
  margin: 0 auto;
  margin-top: 30px;
  margin-right: 20px;
  .tabs {
    margin: 0;
    background-color: #fff;
    border: 1px solid #ddd;
    list-style: none;
    display: flex;
    li {
      padding: 15px 30px;
      margin: 0;
      cursor: pointer;
      &:hover,
      &.active {
        background-color: blueviolet;
        color: #fff;
      }
    }
  }
  .tab_body {
    padding: 20px;
    background-color: #fff;
  }
  .vstr-exit-intent-popup {
    margin: 0 auto;
    padding-top: 0px;
    transform: initial;
    transition: initial;
    position: relative;
    top: initial;
    left: initial;
    right: initial;
    bottom: initial;
    background-color: transparent;
    margin-top: 50px;
    h3,
    p {
      outline: none;
    }
    .vstr_button_item {
      position: relative;
    }
    .popup {
      transform: initial;
      position: initial;
    }

    .popup .submit {
      margin: 0;
    }
  }
}

// custom panel
.b-custom-panel {
  width: 280px;
  float: right;
}
